.ripple {
	position: relative;
	overflow: hidden;

	&:after {
		position: absolute;
		content: "";
		background: rgb(211, 211, 211);
		display: block;
		border-radius: 50%;
		padding-top: 240%;
		padding-left: 240%;
		margin-top: -120%;
		margin-left: -120%;
		opacity: 0;
		transition: all 1s;
	}

	&:active:after {
		position: absolute;
		padding-top: 0;
		padding-left: 0;
		margin-top: 0;
		margin-left: 0;
		opacity: 1;
		transition: 0s;
	}
}

